/*start main style*/

body {
    height: 100vh;
    width: 100%;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 400;
    background-color: #262626;
}

.header {
    width: 100%;
    height: 64px;
    border: 1px solid #E8E8E8;
    border-left: 0px;
    border-right: 0px;
    background-color: #fff;
}

.main {
    height: 100%;
    padding-top: 16px;
    padding-bottom: 16px;
    min-height: 738px;
    background-color: #011428;
}

.footer-nav {
    background-color: transparent;
}

.footer-info {
    background-color: #333333;
}

.logo {
    height: 62px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.logo a {
    line-height: normal;
}

.main-menu ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    height: 62px;
}

.main-menu ul a {
    display: inline-block;
    font-size: 14px;
    line-height: 14px;
    font-weight: 600;
    color: #595959;
    padding: 24px 24px;
    text-decoration: none;
    transition: .1s all linear;
}

.main-menu ul a:hover,
.main-menu ul a:focus,
.main-menu ul a:active {
    background-color: #FAFAFA;
    transition: .1s all linear;
    color: #000;
}

.main-menu ul a.active {
    background-color: #F5F5F5;
    color: #F77705;
    transition: .1s all linear;
}

.header-right-btn {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.user {
    position: relative;
}

.user-menu {
    width: 289px;
    position: absolute;
    right: 0px;
    top: calc(100% + 8px);
    background: #FFFFFF;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    z-index: 100;
    overflow: hidden;
    display: none;
}

.user-menu.visible {
    display: block;
}

.user-menu ul {
    padding: 0px;
    margin: 0px;
    list-style: none;
}

.user-menu a {
    display: block;
    color: #333333;
    font-weight: 600;
    padding: 12px 12px 12px 16px;
    transition: .1s all linear;
}

.user-menu a.out {
    color: #F77705;
}

.user-menu a:hover {
    background-color: #F5F5F5;
    transition: .1s all linear;
    text-decoration: none;
}

.user-btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 11px 8px 11px 16px;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    color: #595959;
    font-weight: 600;
}

.user-btn:hover,
.user-btn:focus,
.user-btn:active {
    outline: none;
}

.user-btn.active {
    background-color: #F5F5F5;
}

.user-foto {
    width: 40px;
    height: 40px;
    background-color: #fff;
    border: 2px solid #F0FAFF;
    border-radius: 50px;
    margin-left: 8px;
}

.user-foto img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.footer-nav {
    padding: 32px 0px;
    min-height: 294px;
}

.footer-nav ul {
    padding: 0px;
    margin: 0px;
    list-style: none;
}

.footer-nav li {
    margin-bottom: 24px;
    text-transform: uppercase;
    color: #8C8C8C;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
}

.footer-nav a {
    text-decoration: none;
    text-transform: none;
    color: #BFBFBF;
    transition: .2s all linear;
    font-weight: 300;
}

.footer-nav a:hover {
    color: #fff;
    text-decoration: none;
    transition: .2s all linear;
}

.footer-info {
    padding: 16px 0px;
}

