    color: #fff;
    background: #F77705;
    border-color: #F77705;
    transition: .2s all linear;
}

.main-btn.orange:hover {
    background: #DA6A05;
    border-color: #DA6A05;
    transition: .2s all linear;
}

.main-btn.orange.active {
    background: #CE6100;
    border-color: #CE6100;
    transition: .2s all linear;
}

.main-btn.success {
    background: #04C01B;
