    border-radius: 50px;
    overflow: hidden;
    position: relative;
}

.radio-wrap label span:after {
    content: '';
    display: block;
    position: absolute;
    top: 4px;
    right: 4px;
    left: 4px;
    bottom: 4px;
    background-color: #04C01B;
    border-radius: 50px;
    transform: scale(0);
    transition: .2s all linear;
}

.radio-wrap input:checked ~ label span {
    background: #fff;
    box-shadow: inset 0px 0px 0px #DDE5EB;
    border: 1px solid #04C01B;
    transition: .2s all linear;
}

.radio-wrap input:checked ~ label span:after {
    transform: scale(1);
    transition: .2s all linear;
}

.radio-wrap.disabled {
    pointer-events: none;
}

.radio-wrap.disabled label span {
    background-color: #F5F5F5;
    box-shadow: none;
}

.radio-wrap.disabled input:checked ~ label span {
    background: #F5F5F5;
    box-shadow: none;
    border: 1px solid #DDE5EB;
    transition: .2s all linear;
}

.radio-wrap.disabled input:checked ~ label span:after {
    background: #BFBFBF;
}

/*end input*/

